import React from 'react';
import { Users, BookOpen, FileText } from 'lucide-react';
import { useTranslation } from '../../contexts/LanguageContext';

const AdminDashboard: React.FC = () => {
  const { t } = useTranslation();
  
  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold text-gray-900">{t('adminDashboard.title')}</h1>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-primary-100 rounded-lg">
              <Users className="h-6 w-6 text-primary-600" />
            </div>
            <div className="ml-4">
              <div className="text-sm font-medium text-gray-500">{t('adminDashboard.totalStudents')}</div>
              <div className="text-2xl font-bold text-gray-900">2,450</div>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-green-100 rounded-lg">
              <BookOpen className="h-6 w-6 text-green-600" />
            </div>
            <div className="ml-4">
              <div className="text-sm font-medium text-gray-500">{t('adminDashboard.activeCourses')}</div>
              <div className="text-2xl font-bold text-gray-900">180</div>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-blue-100 rounded-lg">
              <Users className="h-6 w-6 text-blue-600" />
            </div>
            <div className="ml-4">
              <div className="text-sm font-medium text-gray-500">{t('adminDashboard.faculty')}</div>
              <div className="text-2xl font-bold text-gray-900">85</div>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-yellow-100 rounded-lg">
              <FileText className="h-6 w-6 text-yellow-600" />
            </div>
            <div className="ml-4">
              <div className="text-sm font-medium text-gray-500">{t('adminDashboard.programs')}</div>
              <div className="text-2xl font-bold text-gray-900">12</div>
            </div>
          </div>
        </div>
      </div>

      <div className="card">
        <h2 className="text-lg font-semibold text-gray-900 mb-4">{t('adminDashboard.recentActivities')}</h2>
        <div className="space-y-3">
          <div className="flex items-center p-3 bg-gray-50 rounded-lg">
            <div className="flex-1">
              <div className="font-medium text-gray-900">{t('adminDashboard.krsPeriod')}</div>
              <div className="text-sm text-gray-500">{t('adminDashboard.opensIn3Days')}</div>
            </div>
            <div className="text-sm font-medium text-yellow-600">{t('adminDashboard.upcoming')}</div>
          </div>
          <div className="flex items-center p-3 bg-gray-50 rounded-lg">
            <div className="flex-1">
              <div className="font-medium text-gray-900">{t('adminDashboard.gradeReports')}</div>
              <div className="text-sm text-gray-500">{t('adminDashboard.completed85')}</div>
            </div>
            <div className="text-sm font-medium text-green-600">{t('adminDashboard.inProgress')}</div>
          </div>
          <div className="flex items-center p-3 bg-gray-50 rounded-lg">
            <div className="flex-1">
              <div className="font-medium text-gray-900">{t('adminDashboard.facultyReview')}</div>
              <div className="text-sm text-gray-500">{t('adminDashboard.scheduledNextWeek')}</div>
            </div>
            <div className="text-sm font-medium text-blue-600">{t('adminDashboard.scheduled')}</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdminDashboard;
